.header{
    width: 100%;
    height: 113px;
    background: url('../img/header/header__bg.png') no-repeat, #000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 40px;
}

.header__logo{
    display: flex;
    align-items: center;

    p{
        margin-left: 20px;
        color: white;
        font-weight: 500;
        font-size: 14px;
    }
}

ul.header__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 500px;
    margin-left: 10%;
    
    li.header__nav__item{

       a{
           color: white;
           font-weight: 500;
           font-size: 14px;

           /* &:hover{
               color: rgb(187, 187, 187);
               transition: .3s color ease;
           } */
       }
    }
}

.header__callback{
    display: flex;
    flex-direction: column;
    text-align: right;
    position: relative;

    &::before{
        content: url('../img/header/call.png');
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -80px;
    }

    a.header__callback__number__mob{
        display: none;
    }

    a.header__callback__number{
        font-weight: 700;
        color: white;
        font-size: 24px;
    }

    a.header__callback__order{
        font-weight: 500;
        color: white;
        font-size: 12px;
        margin-top: 5px;
        &:hover{
            color: black;
            transition: .3s color ease;
        }
    }
}

.header__burger{
    display: none;
}

.header__burger{
    cursor: pointer;
    width: 22px;
    height: 16px;
    position: relative;
    justify-content: center;
    align-items: center;

    span{
        width: 100%;
        height: 2px;
        border-radius: 3px;
        background-color: #fff;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            border-radius: 3px;
            background-color: #fff;
            bottom: -7px;
            transition: .3s ease;
        }

        &::before{
            content: '';
            position: absolute;
            top: -7px;
            left: 0;
            width: 100%;
            height: 2px;
            border-radius: 3px;
            background-color: #fff;
            transition: .3s ease;
        }
    }
}

/*    Mobile    */ 
@media (max-width: 990px){
    .header{
        height: 70px;
        background-image: none;
        padding: 0 20px;
    }

    .header__burger{
        display: flex;
    }

    .header__menu{
        order: 3;
        .header__nav{
            display: none;
        }

        &.menu__active{

            .header__burger{
                
                span{
                    background-color: transparent;

                    &::before{
                        top: 0;
                        transform: rotate(135deg);
                    }

                    &::after{
                        bottom: 0;
                        transform: rotate(-135deg);
                    }
                }
            }

            .header__nav{
                display: flex;
                position: absolute;
                top: 70px;
                left: 0;
                margin: 0;
                background-color: black;
                width: 100%;
                height: calc(100vh - 70px);
                z-index: 25;
                flex-direction: column;
                justify-content: flex-start;

                .header__nav__item{
                    margin-top: 10%;
                }
            }
        }
    }

    .header__logo{
        order: 2;

        img{
            width: 124px;
            height: 46px;
        }

        p{
            display: none;
        }
    }

    .header__callback{
        order: 1;
    }

    .header__callback{
        display: flex;
        flex-direction: column;
        text-align: right;
        position: relative;
    
        &::before{
            display: none;
        }

        a.header__callback__number__mob{
            display: block;
        }
    
        a.header__callback__number{
            display: none;
        }
    
        a.header__callback__order{
            display: none;
        }
    }

}