.intro{
    width: 100%;
    height: 775px;
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
}

.intro__bg{
    width: 100%;
    height: 775px;
    position: absolute;
    top: 0;
    left: 0;
    user-select: none;
    pointer-events: none;
    z-index: 5;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.intro__contents{
    width: 100%;
    position: relative;
    z-index: 15;
    margin-left: 12%;
}

.intro__slogan{

    p.slogan{
        font-size: 64px;
        color: black;
        font-weight: 700;
        line-height: 130%;
        
        span{
            color: white;
            position: relative;
            &::after{
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                left: 2px;
                top: 0;
                background-color: #D40050;
                z-index: -1;
            }
        }
    }
}

.get__gifts__text{
    display: flex;
    align-items: center;
    margin-top: 30px;

    p.text{
        width: 478px;
        font-size: 20px;
        font-weight: 500;
        line-height: 130%;
    }
}

.get__gifts__buttons{
    display: flex;
    align-items: center;
    margin-top: 30px;

    p.text{
        margin-left: 40px;
        font-size: 16px;
        font-weight: 400;
        color: #D40050;
        position: relative;

        &::after{
            content: url('../img/svg/intro/arrow.svg');
            position: absolute;
            display: block;
            top: 50px;
            left: -80px;
        }
    }

    .btn__calc__price{
        cursor: pointer;
        width: 250px;
        height: 53px;
        background-color: #D40050;
        font-size: 16px;
        font-weight: 500;
        color: white;
        position: relative;
        transform: translate(7px, -7px);
        transition: .15s ease;

        &::after{
            content: '';
            width: 100%;
            height: 100%;
            border: 2px #D40050 solid;
            position: absolute;
            top: 7px;
            left: -7px;
            transition: .15s ease;
        }

        &:hover{
            transform: translate(0, 0);
            transition: .15s linear;

            &::after{
                top: 0;
                left: 0;
                transition: .15s linear;
            }
        }
    }
}

.intro__benefits{
    display: flex;
    align-items: center;
    margin-top: 100px;

    .intro__benefits__item{
        display: flex;
        align-items: center;
        margin-left: 40px;

        &:first-child{
            margin-left: 0;
        }

        p{
            margin-left: 15px;
            font-weight: 500;
            line-height: 130%;
            font-size: 16px;
        }
    }
}

/*    Mobile    */ 
@media (max-width: 990px){
    .intro__bg{
        height: 383px;
    
        img{
            object-fit: 100%;
        }
    }

    .intro__slogan{

        p.slogan{
            font-size: 24px;
            
            span{
                &::after{
                    left: 1px;
                }
            }
        }
    }
    
    .get__gifts__text{
    
        p.text{
            width: 478px;
            font-size: 16px;
        }
    }

    .intro__contents{
        margin-left: 0;
        padding: 0 20px;
    }

    .get__gifts__buttons{
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        margin-top: 30px;
    
        p.text{
            margin-left: 0;
            margin-top: 30px;
            align-self: flex-end;
    
            &::after{
                content: url('../img/svg/intro/arrow.svg');
                position: absolute;
                display: block;
                top: 40%;
                transform: translateX(-40%);
                left: -80px;
            }
        }
    
        .btn__calc__price{
            cursor: pointer;
            width: 100%;
            height: 53px;
            background-color: #D40050;
            font-size: 16px;
            font-weight: 500;
            color: white;
            position: relative;
            transform: translate(7px, -7px);
            transition: .15s ease;
    
            &::after{
                content: '';
                width: 100%;
                height: 100%;
                border: 2px #D40050 solid;
                position: absolute;
                top: 7px;
                left: -7px;
                transition: .15s ease;
            }
    
            &:hover{
                transform: translate(0, 0);
                transition: .15s linear;
    
                &::after{
                    top: 0;
                    left: 0;
                    transition: .15s linear;
                }
            }
        }
    }
}

@media (max-width: 478px){
    .intro__slogan{

        p.slogan{
            font-size: 16px;
            
            span{
                &::after{
                    left: 1px;
                }
            }
        }
    }
    
    .get__gifts__text{
    
        p.text{
            width: 100%;
            font-size: 14px;
        }
    }
}